import React, { Component, Suspense } from 'react';
import { toast } from "react-toastify";
import { Tab, Nav, OverlayTrigger, Tooltip, Dropdown, InputGroup, DropdownButton, Form } from 'react-bootstrap';
import { COPY_TO_CLIPBOARD } from '../../../Services/Utils/utils';
import { EmbedURL, EMBED_MIN } from '../../../Services/Api'
import { mailSendRequest } from '../../../Services/Actions/mailActions'
const ShareEmbedModal = React.lazy(() => import('./ShareEmbedModal'));

class EmbedCode extends Component {
    constructor() {
        super();
        this.state = { showModal: false, codeCopied: false, isLoading: false, widthSign: "%", heightSign: "%", width: "100", height: "100" };
    }
    shareEmbedModal = event => this.setState({
        showModal: true
    })
    shareEmbedCallback = (shareEmbedEvent) => {
        this.setState({
            showModal: shareEmbedEvent
        })
    }

    codeCopy = (id) => {
        this.setState({
            codeCopied: true
        })
        setTimeout(() => {
            this.setState({
                codeCopied: false
            })
        }, 1500);
        COPY_TO_CLIPBOARD(id);
    }
    onClickToShare = (mailId) => event => {
        var self = this;
        this.setState({ isLoading: true })
        if (mailId && String(mailId).length > 0) {
            mailSendRequest({ htmlCode: document.querySelector("#embedCode_").innerText, email: mailId, type: 3 }).then((response) => {
                toast.success("Taggbox widget code successfully sent.")
                self.setState({ showModal: false, isLoading: false })
            })

        }
        else {
            toast.error("Please enter email id.")
        }
    }
    onChangeWidth = event => {
        if (event.target.value && parseInt(event.target.value) > 0) this.setState({ width: event.target.value })
    }
    onChangeHeight = event => {
        if (event.target.value && parseInt(event.target.value) > 0) this.setState({ height: event.target.value })
    }
    onClickToWidthSign = (value) => event => {
        this.setState({ widthSign: value })
    }
    onClickToHeightSign = (value) => event => {
        this.setState({ heightSign: value })
    }
    render() {
        const { codeCopied, showModal, width, height, widthSign, heightSign } = this.state
        const { copyText, data } = this.props

        return <div className="embed_code">
            <div className="mb-5">
                <div className="row">
                    <div className="col">
                        <InputGroup>
                            <InputGroup.Text>Width</InputGroup.Text>
                            <Form.Control type="number" defaultValue={width} value={width} onChange={this.onChangeWidth} />
                            <DropdownButton align="end" variant="outline-secondary" title="%">
                                <Dropdown.Item href="#" onClick={this.onClickToWidthSign("%")}>Percent (%)</Dropdown.Item>
                                <Dropdown.Item href="#" onClick={this.onClickToWidthSign("px")}>Pixel (PX)</Dropdown.Item>
                                <Dropdown.Item href="#" onClick={this.onClickToWidthSign("vw")} >Viewport Width (VW)</Dropdown.Item>
                            </DropdownButton>
                        </InputGroup>
                    </div>
                    <div className="col">
                        <InputGroup>
                            <InputGroup.Text>Height</InputGroup.Text>
                            <Form.Control type="number" defaultValue={height} value={height} onChange={this.onChangeHeight} />
                            <DropdownButton align="end" variant="outline-secondary" title="%">
                                <Dropdown.Item href="#" onClick={this.onClickToHeightSign("%")}>Percent (%)</Dropdown.Item>
                                <Dropdown.Item href="#" onClick={this.onClickToHeightSign("px")}>Pixel (PX)</Dropdown.Item>
                                <Dropdown.Item href="#" onClick={this.onClickToHeightSign("vw")}>Viewport Height (VH)</Dropdown.Item>
                            </DropdownButton>
                        </InputGroup>
                    </div>
                </div>
            </div>
            <h5 className="mb-3 h6">{copyText}</h5>
            <div className="p-3 bg-light rounded-1 mb-5">
                <Tab.Container id="left-tabs-example" defaultActiveKey="liteCodeEmbed_">
                    <Nav variant="nav" className="mb-3">
                        <Nav.Item className="me-4">
                            <Nav.Link eventKey="liteCodeEmbed_" className="px-0 fw-semibold">
                                Lite Code
                            </Nav.Link>
                        </Nav.Item>
                        <Nav.Item className="">
                            <Nav.Link eventKey="iframeCodeEmbed_" className="px-0 fw-semibold">
                                Iframe
                            </Nav.Link>
                        </Nav.Item>
                    </Nav>
                    <Tab.Content>
                        <Tab.Pane eventKey="liteCodeEmbed_">
                            <div className="mb-2 mb-xl-3 position-relative">
                                <code className="form-control textarea-min-row-4 p-lg-4" id="embedCode_" onClick={() => { this.codeCopy('embedCode_') }} style={{ wordBreak: 'break-all' }}>
                                    &lt;div class="taggbox" data-widget-id="{data && Object.keys(data).length > 0 ? data.wallId : null}" ugcSuite="1" style="width:{width}{widthSign};height:{height}{heightSign};"&gt;&lt;/div&gt;
                                    &lt;script src={EMBED_MIN} type="text/javascript"&gt;&lt;/script&gt;
                                </code>
                                <div className="emebed_action position-absolute end-0 bottom-0 pe-2 pb-2">
                                    <OverlayTrigger placement="top" overlay={<Tooltip>{!codeCopied ? 'Copy Code' : 'Code Copied'}</Tooltip>}>
                                        <button type="button" className="btn text-gray-500 p-0 me-2" onClick={() => { this.codeCopy('embedCode_') }}>
                                            {!codeCopied ?
                                                <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" viewBox="0 0 18.17 18.17">
                                                    <path id="Path_2" data-name="Path 2" d="M-932.08-463.83a2.92,2.92,0,0,1-2.92-2.92v-9.086a2.924,2.924,0,0,1,1.947-2.752v9.567a3.245,3.245,0,0,0,3.245,3.244h9.567a2.924,2.924,0,0,1-2.753,1.947Zm3.245-3.244a2.92,2.92,0,0,1-2.92-2.921v-9.085a2.92,2.92,0,0,1,2.92-2.92h9.086a2.92,2.92,0,0,1,2.92,2.92V-470a2.92,2.92,0,0,1-2.92,2.921Z" transform="translate(935 482)" fill="currentColor" />
                                                </svg>
                                                :
                                                <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" width="20" height="20">
                                                    <path fill="none" d="M0 0h24v24H0z" />
                                                    <path d="M10 15.172l9.192-9.193 1.415 1.414L10 18l-6.364-6.364 1.414-1.414z" fill="#6AC259" />
                                                </svg>
                                            }
                                        </button>
                                    </OverlayTrigger>
                                    <OverlayTrigger placement="top" overlay={<Tooltip>Share Code</Tooltip>}>
                                        <button className="btn text-gray-500 p-0 GetShareCode" type="button" onClick={this.shareEmbedModal}>
                                            <svg xmlns="http://www.w3.org/2000/svg" width="20" height="16" viewBox="0 0 20.765 16.086">
                                                <path id="Path_1" data-name="Path 1" d="M-946.936-459.319A3.066,3.066,0,0,1-950-462.383V-471.3l9.755,5.956a1.179,1.179,0,0,0,1.227,0l9.783-5.956v8.917a3.066,3.066,0,0,1-3.065,3.065ZM-949.9-473a3.047,3.047,0,0,1,2.966-2.4H-932.3a3.045,3.045,0,0,1,2.965,2.4l-10.27,6.275Z" transform="translate(950 475.404)" fill="currentColor" />
                                            </svg>
                                        </button>
                                    </OverlayTrigger>
                                </div>
                            </div>
                        </Tab.Pane>
                        <Tab.Pane eventKey="iframeCodeEmbed_">
                            <div className="mb-2 mb-xl-3 position-relative">
                                <code className="form-control textarea-min-row-4 p-lg-4" id="embedCodeIframe_" onClick={() => { this.codeCopy('embedCodeIframe_') }}>
                                    &lt;iframe src="{EmbedURL}{data && Object.keys(data).length > 0 ? data.wallId : null}" style="width:100%;height:600px;border:none;"&gt;&lt;/iframe&gt;
                                </code>
                                <div className="emebed_action position-absolute end-0 bottom-0 pe-2 pb-2">
                                    <OverlayTrigger placement="top" overlay={<Tooltip>{!codeCopied ? 'Copy Code' : 'Code Copied'}</Tooltip>}>
                                        <button type="button" className="btn text-gray-500 p-0 me-2" onClick={() => { this.codeCopy('embedCodeIframe_') }}>
                                            {!codeCopied ?
                                                <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" viewBox="0 0 18.17 18.17">
                                                    <path id="Path_2" data-name="Path 2" d="M-932.08-463.83a2.92,2.92,0,0,1-2.92-2.92v-9.086a2.924,2.924,0,0,1,1.947-2.752v9.567a3.245,3.245,0,0,0,3.245,3.244h9.567a2.924,2.924,0,0,1-2.753,1.947Zm3.245-3.244a2.92,2.92,0,0,1-2.92-2.921v-9.085a2.92,2.92,0,0,1,2.92-2.92h9.086a2.92,2.92,0,0,1,2.92,2.92V-470a2.92,2.92,0,0,1-2.92,2.921Z" transform="translate(935 482)" fill="currentColor" />
                                                </svg>
                                                :
                                                <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" width="20" height="20">
                                                    <path fill="none" d="M0 0h24v24H0z" />
                                                    <path d="M10 15.172l9.192-9.193 1.415 1.414L10 18l-6.364-6.364 1.414-1.414z" fill="#6AC259" />
                                                </svg>
                                            }
                                        </button>
                                    </OverlayTrigger>
                                    <OverlayTrigger placement="top" overlay={<Tooltip>Share Code</Tooltip>}>
                                        <button className="btn text-gray-500 p-0 GetShareCode" type="button" onClick={this.shareEmbedModal}>
                                            <svg xmlns="http://www.w3.org/2000/svg" width="20" height="16" viewBox="0 0 20.765 16.086">
                                                <path id="Path_1" data-name="Path 1" d="M-946.936-459.319A3.066,3.066,0,0,1-950-462.383V-471.3l9.755,5.956a1.179,1.179,0,0,0,1.227,0l9.783-5.956v8.917a3.066,3.066,0,0,1-3.065,3.065ZM-949.9-473a3.047,3.047,0,0,1,2.966-2.4H-932.3a3.045,3.045,0,0,1,2.965,2.4l-10.27,6.275Z" transform="translate(950 475.404)" fill="currentColor" />
                                            </svg>
                                        </button>
                                    </OverlayTrigger>
                                </div>
                            </div>
                        </Tab.Pane>
                    </Tab.Content>
                </Tab.Container>
            </div>
            <Suspense fallback={<></>}>
                {showModal ? <ShareEmbedModal shareEmbedCallback={this.shareEmbedCallback} shareEmbedModal={showModal} onClickToShare={this.onClickToShare} isLoading={this.state.isLoading} /> : null}
            </Suspense>
        </div>

    }
}

export default EmbedCode;